
Template.__checkName("friend");
Template["friend"] = new Template("Template.friend", (function() {
  var view = this;
  return HTML.OL(HTML.Raw('\n    <h1 class="text-danger" align="top">好友列表</h1>\n    '), HTML.TABLE({
    "class": "table table-striped table-hover"
  }, "\n        ", HTML.THEAD("\n            ", HTML.TR("\n                ", HTML.TH("＃"), "\n                ", HTML.TD("名次"), "\n                ", HTML.TD("昵称"), "\n                ", HTML.TD("总发布数"), "\n                ", HTML.TD("操作"), "\n            "), "\n        "), "\n        ", HTML.TBODY("\n            ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("friendToday"));
  }, function() {
    return [ "\n                ", HTML.TR("\n                    ", HTML.TH(), "\n                    ", HTML.TD(HTML.LI()), "\n                    ", HTML.TD(HTML.A({
      href: "/self",
      id: "toViewOther"
    }, Blaze.View("lookup:user.username", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("user"), "username"));
    }))), "\n                    ", HTML.TD(Blaze.View("lookup:totalPost", function() {
      return Spacebars.mustache(view.lookup("totalPost"));
    })), "\n                    ", HTML.TD(HTML.BUTTON({
      id: "deleteFriend",
      "class": "btn btn-info"
    }, "删除")), " \n                "), "\n            " ];
  }), "\n        "), "\n    "), "\n    ");
}));
